body {
  background-color: #000;
}
/*
    CSS Rules to specify text
    font-family: 'IBM Plex Mono', monospace;
    font-family: 'VT323', monospace;
*/

.IBM-Plex-Mono {
    font-family: 'IBM Plex Mono', monospace;
}

.VT323 {
    font-family: 'VT323', monospace;
}
/*
For Monochrome green and others, see:
https://superuser.com/questions/361297/what-colour-is-the-dark-green-on-old-fashioned-green-screen-computer-displays/1206781#1206781
*/

:root {
  --retrowave-cyan: #2de2e6;
  --retrowave-blue: #035ee8;
  --retrowave-pink: #f6019d;
  --retrowave-magenta: #d40078;
  --retrowave-purple: #9700cc;
  --background-gray: #121212;
  --monochrome-green: #00ff33;
}

.monochrome-green {
  color: #00ff33;
  color: var(--monochrome-green);
}

.retrowave-blue {
  color: #035ee8 !important;
  color: var(--retrowave-blue) !important;
}

.retrowave-magenta {
  color: #d40078;
  color: var(--retrowave-magenta);
}
